@import "../../scss/mixins.scss";
@import "../../scss/variables.scss";

.hero {
    &__wrapper {
        position: relative;
    }

    &__container {
        max-width: 1050px;
        width: 100%;
        margin: 0 auto;
        @media (min-width: em(600)) {
            min-height: 100vh;
        }

        @media (min-width: $mobile) {
            padding: 0px 15px;
            padding-top: 93px;
        }
    }

    &-slider {
        @media (min-width: $mobile) {
            position: absolute !important;
        }
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }

    &__loading {
        text-align: center;
        margin-top: 100px;
    }

    &__error {
        font-size: 24px;
        font-weight: 500;
        text-align: center;
        margin-top: 70px;
    }
}

.hero-info {
    padding: 36px;
    @include adaptiveValue("padding", 36, 15);
    @media (max-width: $mobile) {
        padding-top: 25px;
    }
    background: rgba(232, 232, 232, 0.66);

    @media (min-width: $mobile) {
        border-radius: 10px;
        max-width: 396px;
        margin-bottom: 79px;
        position: relative;
        z-index: 5;
    }

    &__title {
        font-family: "Inter";
        font-weight: 700;
        @include adaptiveValue("font-size", 36, 24);
        @include adaptiveValue("line-height", 50, 30);
        line-height: rem(50);
        letter-spacing: 0.07em;
        margin-bottom: 10px;
    }

    &__text {
        font-size: rem(18);
        line-height: rem(26);
        @include adaptiveValue("margin-bottom", 38, 20);
    }

    &__btn {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
        padding: 10px 0px;
        background: rgba(197, 255, 253, 0.8);
        border-radius: 10px;
        transition: all 0.3s ease;
        cursor: pointer;
        @media (any-hover: hover) {
            &:hover {
                background-color: rgba(143, 223, 220, 0.8);
                color: #fff;
            }
        }
    }

    @media (min-width: $mobile) and (max-height: 500px) {
        bottom: 50px;
    }
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.swiper-pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    margin-bottom: 68px;
    @include adaptiveValue("margin-bottom", 68, 20);
}

.swiper-pagination-bullet {
    width: 11px;
    height: 11px;
    background-color: #d8d8d8;
}

.swiper-pagination-bullet-active {
    background-color: #8b8bae;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    &::before {
        content: "";
        display: inline-block;
        flex: 0 0 27px;
        height: 27px;
        background-color: transparent;
        border-radius: 50%;
        border: 1px solid #8b8bae;
    }
}
