@import "../../scss/mixins.scss";
@import "../../scss/variables.scss";

.footer {
    background-color: rgba(118, 148, 159, 0.63);
    padding: 40px 0px;

    &__container {
        max-width: 1050px;
        margin: 0 auto;
        display: flex;
        padding: 0px 15px;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
        gap: 20px;
    }
}

.footer-logo {
    @media (max-width: 500px) {
        width: 100%;
    }
    display: flex;
    align-items: center;
    gap: 7px;
    font-size: 14;
    span {
        display: block;
        font-size: 24px;
        font-weight: 600;
        margin-bottom: 5px;
    }
}

.menu-footer {
    &__item {
        display: flex;
        align-items: center;
        padding: 5px 0px;
        &:not(:last-child) {
            margin-bottom: 20px;
        }
        &:hover {
            text-decoration: underline;
        }
    }
}

.footer-social {
    &__item {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
        a {
            display: flex;
            gap: 20px;
            align-items: center;
            span {
                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}

.footer-info {
    @media (max-width: $mobile) {
        margin-top: 15px;
    }
    @media (min-width: $mobile) {
        max-width: 240px;
    }
    p {
        &:not(:last-child) {
            margin-bottom: 12px;
        }
    }
}
